@media (max-width: 1200px) {
  nav {
    .nav_menu {
      .nav_item {
        padding-right: 30px;
        border-right: none;
        .nav_link {
          font-size: 13px; } } } }
  .main_bg {
    .main_header {
      font-size: 50px; }
    .main_subheader {
      font-size: 33px; } }
  .reasons {
    .reasons_wrapper {
      .reasons_block {
        display: flex;
        align-items: flex-start;
        .reasons_txt {
          width: 286px; } } } }
  .mobile {
    &_item {
      width: 300px; } }
  footer {
    .footer_middle {
      margin-right: 14px; }
    .footer_apps {
      .footer_download {
        display: block;
        margin-top: 6px; } } } }

@media (max-width: 991px) {
  nav {
    .nav_menu {
      .nav_item {
        padding-right: 7px;
        .nav_link {
          font-size: 11px; } } } }
  .subheader {
    .main_icon {
      width: 100%; }
    &_txt_1 {
      font-size: 11px; }
    &_tell {
      font-size: 19px; } }
  .main_bg {
    .main_header {
      font-size: 40px; }
    .main_subheader {
      font-size: 25px;
      margin-top: 11px; } }
  .reasons {
    .reasons_header {
      font-size: 31px; }
    .reasons_wrapper {
      margin-left: 0;
      .reasons_block {
        display: flex;
        flex-direction: column;
        align-items: center;
        .reasons_txt {
          width: 262px;
          margin-top: 20px;
          font-size: 14px; } } } }
  .perfect {
    .perfect_car {
      width: 70%; } } }

@media (max-width: 767px) {
  .main_bg {
    .main_header {
      font-size: 33px; } }
  header {
    nav {
      .nav_menu {
        position: fixed;
        flex-direction: column;
        justify-content: center;
        align-items: center;
        top: 0;
        left: -100%;
        width: 50%;
        height: 100vh;
        padding: 20px 15px;
        transition: 0.6s all;
        background-color: rgba(0,0,0,.8);
        text-align: center;
        .nav_item {
          padding-right: none;
          border-right: none;
          padding: 10px 0;
          width: 100%;
          .nav_link {
            font-size: 17px;
            display: block;
            width: 100%; } } }
      .nav_active {
        left: 0; }
      .hamburger {
        display: block;
        width: 25px;
        height: 16px;
        position: absolute;
        right: 23px;
        top: 50%;
        transform: translateY(-50%);
        span {
          display: block;
          width: 100%;
          height: 2px;
          background-color: #fff;
          margin-bottom: 4px;
          transition: 0.5s all; }
        &_active {
          span {
            margin-bottom: -2px;
            &:nth-child(1) {
              transform: translateY(6px) rotate(-45deg); }
            &:nth-child(2) {
              display: none; }
            &:nth-child(3) {
              transform: translateY(6px) rotate(45deg); } } } } }
    .subheader {
      .xs-hidden {
        display: none; }
      .subheader_tell {
        margin-top: 11px;
        font-size: 16px;
        color: #ffffff;
        font-weight: 400;
        text-align: center; }
      &_btn {
        display: block;
        margin: 0 auto;
        width: 200px;
        height: 40px; }
      .main_icon {
        img {
          width: 100%; } }
      .xs-visibled {
        display: block; } } }
  .reasons {
    .reasons_header {
      font-size: 25px; } }
  .mobile {
    .mobile_header {
      font-size: 27px; } }
  .perfect {
    .perfect_header {
      font-size: 27px; }
    .perfect_car {
      width: 100%; } }
  .requir {
    &_header {
      font-size: 29px; }
    &_title {
      font-size: 19px; } }
  .world {
    &_header {
      font-size: 29px; } }
  footer {
    padding: 20px 23px 39px 23px;
    display: block;
    .footer_icons {
      display: none;
      .footer_around {
        margin-top: 4px; } }
    .footer_middle {
      margin: 0 auto;
      &::before {
        content: '';
        display: none; } }
    .footer_apps {
      margin-top: 10px;
      display: block;
      text-align: center;
      .footer_download {
        display: inline-block; } } } }

@media (max-width: 575px) {
  header {
    nav {
      height: 30px;
      .hamburger {
        margin-top: 3px;
        right: 15px;
        span {
          height: 1px;
          width: 22px; }
        &_active {
          span {
            margin-bottom: -1px; } } }
      .nav_menu {
        width: 80%;
        height: 100%; } }
    .subheader {
      margin-top: 30px;
      height: 61px;
      .main_icon {
        margin-top: 17px; }
      &_btn {
        width: 110px;
        height: 22px;
        font-size: 10px;
        margin-top: 7px; }
      &_txt_1 {
        display: none; }
      .subheader_tell {
        font-size: 12px;
        margin-top: 11px;
        line-height: 12px; } } }
  .main_bg {
    min-height: 400px;
    padding: 143px 0 35px 0;
    background: url('../img/main_bg_2.png') center center/cover no-repeat;
    .main_txt {
      display: none; }
    .main_btn {
      margin-top: 72px;
      width: 210px;
      height: 50px;
      font-weight: 400; }
    .main_header {
      font-size: 30px;
      line-height: 39px; }
    .main_subheader {
      font-size: 20px;
      margin-top: 13px;
      line-height: 24px;
      font-weight: 700; } }

  .reasons {
    padding: 21px 0 50px 0;
    .reasons_wrapper {
      // padding: 0 25px
      .reasons_block {
        .reasons_txt {
          width: 100%;
          margin-left: 0; }
        .reasons_round {
          width: 101px;
          height: 101px; } } }
    .reasons_header {
      font-size: 16px;
      line-height: 20px; } }

  .mobile {
    padding: 21px 0 43px 0;
    .mobile_header {
      margin-top: 15px;
      font-size: 16px;
      line-height: 13px; }
    .mobile_subheader {
      margin-top: 11px;
      margin-bottom: 27px;
      font-size: 14px;
      line-height: 21px;
      font-weight: 400; }
    .mobile_item {
      padding: 81px 43px 0 43px;
      width: 289px;
      height: 254px; }
    .mobile_item_subtitle {
      font-size: 18px; } }
  .perfect {
    padding: 25px 0 34px 0;
    min-height: 391px;
    background: url('../img/perfect_bg_2.png') center center/cover no-repeat;
    .perfect_header, .perfect_subheader {
      display: none; }
    .perfect_car {
      margin-top: 35px; }
    .perfect_txt {
      margin-top: 23px;
      font-size: 17px;
      color: #fff;
      line-height: 24px; } }
  .requir {
    padding: 24px 0 26px 0;
    .requir_header {
      font-size: 16px;
      margin-top: 15px;
      line-height: 14px; }
    .requir_block {
      margin-top: 22px;
      margin-bottom: 15px;
      .requir_list {
        li {
          font-size: 15px;
          line-height: 17px;
          &::before {
            top: -1px; } } } }
    .requir_title {
      line-height: 15px;
      font-size: 16px; } }
  .world {
    padding: 25px 0 30px 0;
    min-height: 360px;
    background: url('../img/world_bg_2.png') center center/cover no-repeat;
    .world_btn {
      width: 77px;
      height: 24px;
      font-size: 11px; }
    .world_header {
      font-size: 16px;
      margin-top: 15px;
      line-height: 11px; }
    .world_subheader {
      font-size: 14px;
      margin-top: 13px;
      line-height: 8px; }
    a {
      font-size: 15px;
      width: 100%;
      bottom: 30px; } }
  footer {
    padding: 48px 18px 20px 18px;
    .footer_icons {
      flex-direction: row;
      margin-top: 19px;
      .footer_around {
        margin-right: 8px;
        margin-top: 0; } }
    .footer-visibled {
      display: flex;
      justify-content: center; }
    .footer_apps {
      margin-top: 20px; }
    .footer_middle {
      .footer_russian {
        margin-top: 13px;
        line-height: 10px;
        .russian_link {
          line-height: 10px; } }
      .footer_main {
        padding: 0;
        .main_link {
          padding-right: 16px;
          font-size: 14px; } } }
    .footer_apps {
      margin-top: 20px;
      .footer_download {
        margin: 0;
        width: 80px;
        img {
          width: 100%; } } } } }
