header {
  width: 100%;
  position: absolute; }

nav {
  width: 100%;
  height: 50px;
  padding: 13px 70px;
  position: fixed;
  background-color: #212121;
  z-index: 1;
  .nav_menu {
    display: flex;
    justify-content: space-between;
    list-style-type: none;
    padding-left: 0;
    .nav_item {
      padding-right: 38px;
      border-right: 2px dotted #000;
      &:last-child {
        border-right: none; } }
    .nav_link {
      font-size: 15px;
      color: #ffffff;
      font-weight: 300;
      &:hover {
        text-decoration: none; }
      &:active {
        color: gray; }
