      //   content: ''
      //   display: block
      //   width: 0%
      //   height: 2px
      //   background-color: $color_blue
      //   transition: 0.5s
      // &:hover
      //   text-decoration: none
      //   &::after
 } } }      //     width: 100%

.subheader {
  height: 85px;
  margin-top: 50px;
  background-color: rgba(0, 0, 0, .6);
  .main_icon {
    display: block;
    margin-top: 16px; }
  &_txt_1 {
    margin-top: 6px;
    font-size: 13px;
    color: #fff;
    font-weight: 300; }
  &_txt_2 {
    margin-top: 23px;
    font-size: 12px;
    color: #ffffff;
    font-weight: 300;
    text-align: right; }
  &_tell {
    display: block;
    font-size: 22px;
    color: #ffffff;
    font-weight: 400;
    text-align: right;
    &:hover {
      text-decoration: none; } }
  &_btn {
    width: 172px;
    height: 46px;
    margin-top: 19px;
    font-size: 14px;
    @include btn;
    @include btn_hover; }
  .xs-visibled {
    display: none; } }
.main_bg {
  min-height: 799px;
  padding: 201px 0 135px 0;
  background: url('../img/main_bg.png') center center/cover no-repeat;
  .main_header {
    margin: 0;
    font-size: 60px;
    line-height: 44px;
    color: #ffffff;
    font-weight: 700;
    text-align: center; }
  .main_subheader {
    margin: 0;
    margin-top: 26px;
    font-size: 36px;
    line-height: 27px;
    color: #ffffff;
    font-weight: 700;
    text-align: center; }
  .main_txt {
    margin-top: 23px;
    font-size: 18px;
    line-height: 24px;
    color: #ffffff;
    font-weight: 400;
    text-align: center; }
  .main_btn {
    width: 259px;
    height: 63px;
    display: block;
    margin: 0 auto;
    margin-top: 118px;
    @include btn;
    @include btn_hover; } }

.reasons {
  min-height: 900px;
  padding: 70px 0 67px 0;
  .reasons_wrapper {
    margin-left: 50px; }
  .reasons_btn {
    @include black_btn; }
  .reasons_header {
    margin-top: 38px;
    @include header; }
  .reasons_block {
    min-height: 141px;
    margin-top: 35px;
    display: flex;
    align-items: center;
    .reasons_round {
      width: 113px;
      height: 113px;
      display: flex;
      justify-content: center;
      align-items: center;
      border-radius: 100%;
      background-color: $color_blue; }
    .reasons_txt {
      width: 342px;
      margin-left: 26px;
      font-size: 15px;
      line-height: 21px;
      font-weight: 400;
      text-align: left;
      span {
        font-size: 17px;
        color: $color_blue;
        font-weight: 400;
        text-align: left; } } } }

.mobile {
  min-height: 865px;
  padding: 48px 0 80px 0;
  background: url('../img/mobile_bg.png') center center/cover no-repeat;
  .row {
    margin-top: 32px; }
  .mobile_btn {
    @include black_btn; }
  .mobile_header {
    margin-top: 38px;
    @include header; }
  .mobile_subheader {
    margin-top: 19px;
    @include subheader; }
  &_item {
    width: 320px;
    height: 280px;
    position: relative;
    margin: 0 auto;
    margin-bottom: 19px;
    padding-top: 92px;
    &_1 {
      background: url('../img/mobile_bg_1.png') center center/cover no-repeat; }
    &_2 {
      background: url('../img/mobile_bg_2.png') center center/cover no-repeat; }
    &_3 {
      background: url('../img/mobile_bg_3.png') center center/cover no-repeat; }
    &_4 {
      background: url('../img/mobile_bg_4.png') center center/cover no-repeat; }
    &_5 {
      background: url('../img/mobile_bg_5.png') center center/cover no-repeat; }
    &_6 {
      background: url('../img/mobile_bg_6.png') center center/cover no-repeat; }
    &_subtitle {
      font-size: 24px;
      color: #ffffff;
      font-weight: 400;
      text-align: center;
      text-transform: uppercase; }
    &_plus {
      width: 26px;
      height: 26px;
      display: flex;
      justify-content: center;
      align-items: center;
      position: absolute;
      left: 50%;
      transform: translateX(-50%);
      bottom: 35px;
      border-radius: 100%;
      background-color: #000000;
      cursor: pointer;
      &::before {
        content: '';
        display: block;
        width: 14px;
        height: 2px;
        background-color: $color_blue;
        transition: 0.3s all; }
      &::after {
        content: '';
        display: block;
        width: 2px;
        height: 14px;
        background-color: $color_blue;
        position: absolute;
        left: 50%;
        transform: translateX(-50%);
 }        // transition: 0.3s all
      &:active {
        &::before, &::after {
          background-color: $color_pink; } } } } }

.perfect {
  min-height: 898px;
  padding: 57px 0 138px 0;
  background: url('../img/perfect_bg.png') center center/cover no-repeat;
  &_btn {
    @include black_btn; }
  &_btn_white {
    background-color: #fff;
    color: #070716; }
  &_header {
    margin-top: 32px;
    @include header; }
  &_header_white {
    color: #fff; }
  &_subheader {
    margin-top: 19px;
    @include subheader; }
  &_subheader_white {
    color: #fff; }
  &_car {
    display: block;
    margin: 0 auto;
    margin-top: 56px; }
  &_txt {
    margin: 0 auto;
    margin-top: 4px;
    font-size: 17px;
    color: #222222;
    font-weight: 300;
    text-align: center; } }

.requir {
  padding: 67px 0 94px 0;
  &_btn {
    @include black_btn; }
  &_header {
    margin-top: 33px;
    @include header; }
  &_block {
    margin: 33px 0; }
  &_title {
    font-size: 22px;
    color: $color_blue;
    font-weight: 400; }
  &_list {
    padding-left: 50px;
    li {
      font-size: 17px;
      list-style-type: none;
      font-weight: 300;
      margin-bottom: 10px;
      position: relative;
      &::before {
        content: '';
        width: 21px;
        height: 20px;
        display: block;
        position: absolute;
        left: -32px;
        top: 3px;
        background: url('../icons/check_blue.svg') center center/cover no-repeat; } } }
  &_txt {
    color: $color_blue;
    font-weight: 300; }
  &_nmb {
    margin-bottom: 0; }
  .warning {
    .requir_title {
      color: $color_pink; }
    .requir_list {
      li {
        &::before {
          content: '';
          background: url('../icons/check_red.svg') center center/cover no-repeat; } } } } }

.world {
  min-height: 718px;
  padding: 50px 0;
  background: url('../img/world_bg.png') center center/cover no-repeat;
  position: relative;
  &_btn {
    @include black_btn; }
  &_header {
    margin-top: 32px;
    @include header; }
  &_subheader {
    margin-top: 20px;
    @include subheader; }
  &_btn_white {
    color: #070716;
    background-color: #ffffff; }
  &_btn_title {
    color: #ffffff; }
  a {
    color: #1fbad6;
    font-weight: 400;
    text-align: center;
    text-transform: uppercase;
    display: block;
    text-align: center;
    position: absolute;
    bottom: 50px;
    left: 50%;
    transform: translateX(-50%);
    &:active {
      color: $color_pink;
      text-decoration: none; }
    &:hover {
      text-decoration: none; } } }
footer {
  min-height: 156px;
  padding: 50px 23px 39px 23px;
  display: flex;
  justify-content: space-between;
  .footer_icons {
    display: flex;
    .footer_around {
      width: 33px;
      height: 33px;
      margin-right: 6px;
      display: flex;
      justify-content: center;
      align-items: center;
      background-color: #070716;
      border-radius: 100%;
      cursor: pointer;
      i {
        color: #fff;
        &:hover {
          color: #fff; } }
      &:hover {
        text-decoration: none; } } }
  .footer_middle {
    margin-right: -182px;
    position: relative;
    text-align: center;
    &::before {
      content: '';
      display: block;
      position: absolute;
      top: -32px;
      right: 81px;
      width: 299px;
      height: 4px;
      background-color: #1fbad6; }
    .footer_main {
      padding-left: 15px;
      .main_link {
        font-size: 15px;
        color: #070716;
        font-weight: 300;
        text-transform: uppercase;
        padding-right: 46px;
        &:active {
          color: #555555; }
        &:hover {
          text-decoration: none; } } }
    .footer_support {
      .support_link {
        font-size: 11px;
        color: #999999;
        font-weight: 300;
        text-transform: uppercase;
        padding-right: 21px;
        &:active {
          color: #000; }
        &:hover {
          text-decoration: none; } } }
    .footer_russian {
      margin-top: 14px;
      .russian_link {
        font-size: 11px;
        color: #1fbad6;
        font-weight: 400;
        text-transform: uppercase;
        &:active {
          color: $color_pink; }
        &:hover {
          text-decoration: none; } } } }
  .footer_apps {
    .footer_download {
      display: inline-block;
      margin-left: 6px; } }

  .footer-visibled {
    display: none; } }
