@mixin btn {
  background-image: linear-gradient(0deg, #1eacc7 0%, #4ce2ff 100%);
  border: none;
  border-radius: 4px;
  color: #ffffff;
  font-weight: 400;
  text-align: center;
  filter: drop-shadow(0.707px 0.707px 0px rgba(0,0,0,0.21));
  text-transform: uppercase; }

@mixin black_btn {
  width: 112px;
  height: 24px;
  display: block;
  margin: 0 auto;
  background-color: #000000;
  font-size: 11px;
  color: #ffffff;
  font-weight: 300;
  text-align: center;
  text-transform: uppercase;
  border: none; }

@mixin header {
  margin-bottom: 0;
  line-height: 28px;
  font-size: 38px;
  color: #222222;
  font-weight: 700;
  text-align: center;
  text-transform: uppercase; }

@mixin subheader {
  font-size: 17px;
  color: #222222;
  font-weight: 400;
  text-align: center;
  margin-bottom: 0; }

@mixin btn_hover {
  &:active {
    background-image: none;
    background-color: $color_blue; } }
